@import "normalize.css";
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  font-weight: 400;
}

a {
  color: #ece345;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

img,
svg {
  max-width: 100%;
}

body {
  position: relative;
  min-width: 320px;
  height: 100vh;
  font-family: "Arial", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.4;
  color: #121212;
  overflow-x: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1366px;
  padding: 0 50px;
  margin: 0 auto;
}

button {
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  border-radius: 5px;
}

.footer {
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(255, 255, 255, 0.388);
  font-size: 0.6em;
}
.footer .link {
  color: rgba(255, 255, 255, 0.388);
}

#gradient-canvas {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  opacity: 0.5;
  --gradient-color-1: #4d18b0;
  --gradient-color-2: #ece345;
  --gradient-color-3: #bd1f5e;
  --gradient-color-4: #bf5c92;
  z-index: -1;
}

::-moz-selection {
  color: white;
  background: #ece345;
}

::selection {
  color: white;
  background: #ece345;
}

.no-columns-text {
  padding: 60px 0;
  display: block;
  width: 100%;
  text-align: center;
  color: rgba(255, 255, 255, 0.893);
}

.error {
  display: block;
  padding: 5px 0;
  color: #bd1f5e;
}

.no-cards-text {
  display: block;
  width: 100%;
  padding: 60px 0;
  text-align: center;
  color: rgba(0, 0, 0, 0.555);
}

.manager__columns {
  height: 100%;
}
.manager__column {
  height: auto;
  width: calc(33.3333333333% - 14px);
  display: flex;
  padding: 15px;
  background-color: #f3f5f7;
  border-radius: 7px;
  margin-bottom: 20px;
}

.column__wrapper {
  width: 100%;
  height: 100%;
}
.column__title-remove-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 15px;
}
.column__btn {
  display: flex;
  align-items: center;
  color: rgba(130, 130, 130, 0.719);
  transition: color 0.1s ease-in-out;
}
.column__btn:focus-visible {
  color: #ece345;
  outline: none;
}
.column__btn:hover {
  color: #ece345;
}
.column .plus {
  margin-right: 8px;
  font-size: 28px;
}
.column__add-column-form {
  display: flex;
  align-items: center;
}
.column__add-column-btn {
  margin-right: 12px;
  outline: none;
}
.column__add-column-btn:focus-visible {
  outline: none;
}
.column__add-column-btn:focus-visible span {
  border-color: #ece345;
}
.column__add-column-btn:hover span {
  border-color: #ece345;
}
.column__add-column-btn span {
  display: block;
  width: 11px;
  height: 6px;
  border-bottom: 3px solid black;
  border-left: 3px solid black;
  transform: rotate(-50deg);
  transition: border-color 0.1s ease-in-out;
}
.column__edit-card-form {
  margin-bottom: 15px;
}
.column__edit-card-label {
  width: 100%;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
}
.column__edit-card-input {
  width: 100%;
  min-height: 100px;
  border-radius: 5px;
  padding: 13px;
  outline: none;
  border: none;
  background-color: whitesmoke;
  resize: none;
}
.column__edit-card-input::placeholder {
  color: rgba(130, 130, 130, 0.719);
}
.column__edit-card-btn {
  padding: 10px 15px;
  margin-right: 10px;
  color: white;
  font-weight: 600;
  background-color: lightgray;
}
.column__edit-card-cancel {
  font-size: 38px;
  transform: rotate(45deg);
  color: rgba(130, 130, 130, 0.719);
}
.column__edit-card-btns {
  display: flex;
  align-items: center;
}
.column__add-column-label {
  width: 70%;
  margin-right: 17px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.column__add-column-input {
  width: 100%;
  border-radius: 5px;
  padding: 9px;
  outline: none;
  border: none;
}
.column__add-column-input::placeholder {
  color: rgba(130, 130, 130, 0.719);
}
.column__cancel-column-btn {
  font-size: 28px;
  transform: rotate(45deg);
  transition: color 0.1s ease-in-out;
}
.column__cancel-column-btn:focus-visible {
  outline: none;
  color: #ece345;
}
.column__cancel-column-btn:hover {
  color: #ece345;
}
.column__remove-btn {
  width: 20px;
  height: 20px;
}
.column__remove-btn svg {
  width: 20px;
  height: 20px;
}
.column__remove-btn svg path {
  transition: stroke 0.1s ease-in-out;
}
.column__remove-btn:focus-visible {
  outline: none;
}
.column__remove-btn:focus-visible svg path {
  stroke: #ece345;
}
.column__remove-btn:hover svg path {
  stroke: #ece345;
}
.column__form {
  width: 100%;
}
.column__form-input {
  padding: 15px;
  width: 100%;
  border-radius: 5px;
  border: none;
  outline: none;
  resize: none;
}
.column__form-input::placeholder {
  color: rgba(130, 130, 130, 0.719);
}
.column__add-card-btns {
  display: flex;
  align-items: center;
}
.column__add-card-btn {
  padding: 10px 15px;
  margin-right: 10px;
  color: white;
  font-weight: 600;
  background-color: #5CA84D;
}
.column__cancel-card-btn {
  font-size: 38px;
  transform: rotate(45deg);
  color: rgba(130, 130, 130, 0.719);
  transition: color 0.1s ease-in-out;
}
.column__cancel-card-btn:focus-visible {
  color: #ece345;
  outline: none;
}
.column__cancel-card-btn:hover {
  color: #ece345;
}
.column__title {
  padding-left: 10px;
  font-size: inherit;
  font-weight: 600;
}
.column__card {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 12px;
  margin-bottom: 15px;
  background-color: white;
  border-radius: 5px;
  border-bottom: 1px solid rgb(211, 211, 211);
  cursor: grab;
}
.column__card:last-child {
  margin-bottom: 25px;
}
.column__card--dragged {
  position: absolute;
  transform: scale(1.1) rotate(5deg);
  z-index: 200;
  pointer-events: none;
  user-select: none;
}
.column__text {
  cursor: auto;
}
.column__form-tag-content {
  padding: 7px 13px;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.column__form-tags {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 4px;
}
.column__form-tag-name {
  font-size: 15px;
  font-weight: 600;
  color: white;
}
.column__form-tag {
  margin-right: 7px;
  margin-bottom: 7px;
  border-radius: 5px;
  background-color: lightgray;
  transition: background-color 0.1s ease-in-out;
}
.column__form-tag:last-child {
  margin-right: 0;
}
.column__tags {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 8px;
}
.column__tag {
  height: 13px;
  width: 45px;
  border-radius: 8px;
  background-color: lightgray;
  margin-right: 7px;
  overflow: hidden;
  cursor: auto;
}
.column__tag--opened {
  height: auto;
  width: auto;
  padding: 1px 13px;
  border-radius: 5px;
}
.column__tag:last-child {
  margin-right: 0;
}
.column__tag-content {
  display: flex;
  align-items: center;
}
.column__tag-name {
  margin-right: 10px;
  font-size: 15px;
  font-weight: 600;
  color: white;
}
.column__tag-btn {
  font-size: 28px;
  color: white;
  transform: rotate(45deg);
}
.column__text {
  margin-bottom: 15px;
}
.column__menu-btn {
  width: 21px;
  height: 21px;
}
.column__menu-btn svg {
  width: 21px;
  height: 21px;
  transform: rotate(180deg);
}
.column__menu-btn svg path {
  transition: stroke 0.1s ease-in-out;
}
.column__menu-btn:focus-visible {
  outline: 2px rgb(66, 66, 66) solid;
  outline-offset: 2px;
}
.column__menu {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 5px;
  overflow: hidden;
  z-index: 100;
  box-shadow: 20px 20px 40px -6px rgba(0, 0, 0, 0.047);
  box-shadow: 1px 0px 23px -5px rgba(34, 60, 80, 0.142);
}
.column__menu--shown {
  animation: open 0.15s ease-in-out;
}
.column__menu--hidden {
  animation: close 0.15s ease-in-out;
}
.column__menu-item {
  border-bottom: 1px solid rgb(209, 209, 209);
  background-color: white;
}
.column__menu-item:last-child {
  border-bottom: none;
}
.column__menu-item-btn {
  padding: 13px 15px;
  width: 100%;
  border-radius: 0;
  text-align: left;
  color: black;
}

@keyframes open {
  0% {
    transform: scale(0.8) translate(-50px, -20px);
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: scale(1) translate(0, 0);
  }
}
@keyframes close {
  0% {
    opacity: 1;
    transform: scale(1) translate(0, 0);
  }
  100% {
    transform: scale(0.8) translate(-50px, -20px);
    opacity: 0;
  }
}
.header {
  padding: 20px 0 40px 0;
}
.header__wrapper {
  display: flex;
  align-items: center;
  align-items: stretch;
  justify-content: space-between;
}
.header__left {
  display: flex;
  align-items: center;
  align-items: stretch;
}
.header__right {
  display: flex;
  align-items: center;
  align-items: stretch;
}
.header__name {
  display: flex;
  align-items: center;
}
.header__btn {
  background: #f3f5f7;
}
.header__add-tag-btn {
  padding: 0 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 15px;
}
.header__add-tag-btn svg {
  width: 22px;
  height: 22px;
  transform: rotate(-120deg);
  transition: transform 0.1s ease-in-out;
}
.header__add-tag-btn--flipped svg {
  transform: rotate(-240deg);
}
.header__add-column-btn {
  margin-right: 15px;
  padding: 0 13px;
  display: flex;
  align-items: center;
}
.header__add-column-btn span {
  font-size: 26px;
}
.header__change-background-btn {
  padding: 7px;
  margin-right: 15px;
  display: flex;
  align-items: center;
}
.header__change-background-btn svg {
  max-width: 25px;
  max-height: 25px;
  min-width: 25px;
  min-height: 25px;
  display: flex;
}
.header__change-background-btn svg:first-child {
  margin-right: 5px;
}
.header__change-background-btn svg:last-child {
  max-width: 15px;
  max-height: 15px;
  min-width: 15px;
  min-height: 15px;
}
.header__clear-btn {
  padding: 7px;
}
.header__clear-btn svg {
  width: 22px;
  height: 22px;
  display: flex;
}
.header__name {
  font-size: 21px;
  font-weight: 600;
  color: white;
}
.header__arrow-svg--flipped {
  transform: rotate(180deg);
}
.header__tag-menu {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 5px;
  overflow: hidden;
  z-index: 100;
  box-shadow: 20px 20px 40px -6px rgba(0, 0, 0, 0.047);
  background-color: white;
  opacity: 0;
  visibility: hidden;
  transform: scale(0.8) translate(-50px, -20px);
  transition-property: opacity, visibility, transform;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}
.header__tag-menu--shown {
  opacity: 1;
  visibility: visible;
  transform: none;
}
.header__tag-form-wrapper {
  padding: 16px 14px;
}
.header__tag-form {
  width: 100%;
  display: flex;
  align-items: center;
  align-items: stretch;
}
.header__tag-btns {
  margin-left: auto;
  display: flex;
  align-items: center;
}
.header__tag-form-btn {
  margin-right: 12px;
  outline: none;
}
.header__tag-form-btn:focus-visible {
  outline: none;
}
.header__tag-form-btn:focus-visible span {
  border-color: #ece345;
}
.header__tag-form-btn:hover span {
  border-color: #ece345;
}
.header__tag-form-btn span {
  display: block;
  width: 11px;
  height: 6px;
  border-bottom: 3px solid black;
  border-left: 3px solid black;
  transform: rotate(-50deg);
  transition: border-color 0.1s ease-in-out;
}
.header__tag-name-input {
  width: 65%;
  margin-right: 7px;
  border-radius: 5px;
  padding: 11px;
  outline: 0;
  border: none;
  background-color: #f5f5f5;
}
.header__tag-item {
  padding: 13px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__tag-name {
  color: white;
  font-weight: 600;
}
.header__tag-remove {
  color: white;
  font-size: 28px;
  transform: rotate(45deg);
  outline: none;
  transition: color 0.1s ease-in-out;
}
.header__tag-remove:focus-visible {
  color: black;
}
.header__tag-remove:hover {
  color: black;
}
.header__header-tag-label {
  display: block;
  display: flex;
  align-items: center;
}
.header__tag-color-input {
  cursor: pointer;
}
.header__tag-form-cancel {
  font-size: 28px;
  transform: rotate(45deg);
  outline: none;
  transition: color 0.1s ease-in-out;
}
.header__tag-form-cancel:focus-visible {
  color: #ece345;
}
.header__tag-form-cancel:hover {
  color: #ece345;
}
.header__background-list {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 5px;
  overflow: hidden;
  z-index: 100;
  box-shadow: 20px 20px 40px -6px rgba(0, 0, 0, 0.047);
  opacity: 0;
  visibility: hidden;
  transform: scale(0.8) translate(-50px, -20px);
  transition-property: opacity, visibility, transform;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}
.header__background-list--shown {
  opacity: 1;
  visibility: visible;
  transform: none;
}
.header__background-item {
  border-bottom: 1px solid rgb(209, 209, 209);
  background-color: white;
}
.header__background-item:last-child {
  border-bottom: none;
}
.header__background-color-label {
  cursor: pointer;
  display: block;
  padding: 13px 15px;
}
.header__background-color-label span {
  margin-right: 7px;
}
.header__background-color {
  cursor: pointer;
}
.header__background-btn {
  padding: 13px 15px;
  width: 100%;
  border-radius: 0;
  text-align: left;
  color: black;
}
.header__background-gradient span {
  background: rgb(255, 130, 130);
  background: -moz-linear-gradient(left, rgb(255, 130, 130) 0%, rgb(225, 120, 237) 100%);
  background: -webkit-linear-gradient(left, rgb(255, 130, 130) 0%, rgb(225, 120, 237) 100%);
  background: linear-gradient(to right, rgb(255, 130, 130) 0%, rgb(225, 120, 237) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ff8282", endColorstr="#e178ed", GradientType=1);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.loader-wrapper {
  height: 100%;
  width: 100%;
  padding-top: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loader {
  border: 9px solid rgba(235, 235, 235, 0.8470588235);
  border-top: 9px solid #f099c2;
  border-radius: 50%;
  width: 55px;
  height: 55px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.hidden {
  display: none;
}

.transparent {
  opacity: 0;
}

.grabbing {
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  cursor: grabbing;
}

@media (max-width: 968px) {
  .manager__column {
    width: calc(50% - 10px);
    margin-bottom: 15px;
  }
  .container {
    padding: 0 40px;
  }
}
@media (max-width: 668px) {
  .manager__column {
    width: 100%;
    margin-bottom: 15px;
  }
  .container {
    padding: 0 30px;
  }
}
@media (max-width: 576px) {
  .header__tag-menu {
    max-width: 250px;
  }
  .header__name {
    display: none;
  }
  .header__tag-form {
    flex-wrap: wrap;
  }
  .header__tag-name-input {
    width: 100%;
    margin-right: 0;
    margin-bottom: 7px;
  }
  .container {
    padding: 0 15px;
  }
}